@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:400,700,800,900&display=swap);
* {
  font-family: 'Nunito Sans', sans-serif;
  line-height: 1.5;
}

body {
  margin: 0px;
}

hr {
  margin: 0px;
  margin-top: 32px;
}

h1 {
  font-size: 60px;
  font-weight: normal;
  margin: 0px;
  margin-bottom: 28px;
  padding-top: 48px;
}

h2 {
  font-weight: normal;
  font-size: 20px;
}

.title-wrapper {
  margin-left: 400px;
}

.projects-wrapper {
  text-align: justify;
  margin-left: 400px;
}

@media only screen and (max-width: 1160px) {
  .projects-wrapper {
    margin-left: 48px;
  }

  .title-wrapper {
    margin-left: 48px;
  }
}
:root {
    --navbar-element-margin: 16px 0px 16px 16px;
    --navbar-item-margin: 16px 16px 16px 0px;
    --navbar-item-font-size: 16px;
    --navbar-name-font-size: 18px;
}

.navbar {
    margin: 0;
    padding: 0;
    background-color: white;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    align-items: center;
}
  
.navbar-name {
    margin: 16px 0px 16px 16px;
    margin: var(--navbar-element-margin);
    font-weight: bold;
    font-size: 18px;
    font-size: var(--navbar-name-font-size);
    transition: 0.4s;
}

.navbar-item {
    margin: 16px 16px 16px 0px;
    margin: var(--navbar-item-margin);
    font-size: 16px;
    font-size: var(--navbar-item-font-size);
}

.navbar-space {
    flex: 1 1;
}
.side-menu {
    border: 1px solid grey;
    width: 279px;
    top: 0px;
    bottom: 0px;
    position: fixed;
    overflow-y: auto;
    background-color: white;
}

.side-menu-name {
    font-weight: bold;
    font-size: 18px;
    padding: 16px;
    padding-left: 24px;
    cursor: pointer;
}

.side-menu-item {
    padding: 12px;
    padding-left: 24px;
    cursor: pointer;
}

.side-menu-entry {
    padding: 12px;
    padding-left: 48px;
    cursor: pointer;
}

.side-menu-item:hover {
    background-color: whitesmoke;
}

.side-menu-entry:hover {
    background-color: whitesmoke;
}

@media only screen and (max-width: 1160px) {
    .side-menu {
        visibility: hidden;
    }
}
.media-demo {
    width: auto;
    height: 512px;
    display: block;
    margin: 48px auto;
    border-width: 2px;
    border-style: solid;
    border-color: black;
    border-radius: 5px;
}

@media only screen and (max-width: 1160px) {
    .media-demo {
        max-width: 100%;
        height: auto;
        max-height: 512px;
    }
}

.project {
    max-width: 760px;
}

.link-wrapper {
    color: black;
    text-decoration: none;
}

.link-text {
    color: blue;
    text-decoration: none;
    margin: auto 12px;
    align-items: center;    

}

.link-icon {
    margin: 16px;
}

.link-container {
    display: flex;
    box-shadow: 0 0 3px #888888;    
    text-align: left;
    align-items: center;
    margin-right: 16px;
    margin-bottom: 16px;
    border-radius: 3px;
}

.link-container:hover {
    background-color: whitesmoke;
}

.divider {
    border-left: 1px solid lightgray;
    height: 44px;
}


.links-container {
    display: flex;
    flex-wrap: wrap;
}

.dark {
    color: white;
}

